 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="app-container">
    <el-card class="operate-container product_detail"
             shadow="never">
      <el-form label-width="180px" :model="productDetails" :inline="true">
        <el-form-item label="商品名称：">
          <span>{{ productDetails.name }}</span>
        </el-form-item>
        <el-form-item label="规格值：">
          <span>{{ productDetails.skuName ? productDetails.skuName : '-' }}</span>
        </el-form-item>
        <el-form-item label="规格编码：">
          <span>{{ productDetails.skuCode ? productDetails.skuCode : '-' }}</span>
        </el-form-item>
        <el-form-item label="当前结余库存：">
          <span>{{ productDetails.remainNum }}</span>
        </el-form-item>
        <el-form-item label="商品编码：">
          <span>{{ productDetails.productCode ? productDetails.productCode : '-' }}</span>
        </el-form-item>
        <el-form-item label="平台分类：">
          <span>{{ productDetails.categoryName3 }}</span>
        </el-form-item>
      </el-form>
    </el-card>
    <!-- 筛选条件 -->
    <el-card class="operate-container"
             style="margin-top:10px"
             shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right"
                   @click="searchList"
                   type="primary"
                   size="small">
          查询
        </el-button>
        <el-button style="float: right;margin-right: 15px"
                   @click="reset"
                   size="small">
          重置
        </el-button>
      </div>
      <div style="margin-top: 40px">
        <el-form :inline="true"
                 size="small"
                 label-width="140px">
          <el-form-item label="统计时间：">
            <el-date-picker v-model="list.time"
                            value-format="yyyy-MM-dd"
                            type="daterange"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <el-card class="operate-container"
             style="margin-top:10px"
             shadow="never">
      <i class="el-icon-tickets"></i>
      <span>数据列表</span>
    </el-card>

    <el-card class="operate-container"
             style="margin-top:10px">
      <el-table :data="tableData"
                style="width: 100%"
                border>
        <el-table-column type="index"
                         width="50"
                         align="center">
        </el-table-column>
        <el-table-column label="交易日期"
                         width="200"
                         align="center">
          <template slot-scope="scope">{{scope.row.createTime}}</template>
        </el-table-column>
        <el-table-column label="业务类型"
                         width="140"
                         align="center">
          <template slot-scope="scope">
            <el-tag type="success"
                    v-if="scope.row.type == 0">上货入库</el-tag>
            <el-tag type="success"
                    v-if="scope.row.type == 1">退货入库</el-tag>
            <el-tag type="danger"
                    v-if="scope.row.type == 2">其他入库</el-tag>
            <el-tag type="success"
                    v-if="scope.row.type == 3">销售出库</el-tag>
            <el-tag type="success"
                    v-if="scope.row.type == 4">其他出库</el-tag>
            <el-tag type="success"
                    v-if="scope.row.type == 5">盘盈入库</el-tag>
            <el-tag type="success"
                    v-if="scope.row.type == 6">盘亏出库</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="出/入库方向"
                         width="150"
                         prop="description"
                         align="center">
        </el-table-column>
        <el-table-column label="出/入库数量"
                         width="180"
                         prop="totalStockChange"
                         align="center">
        </el-table-column>
        <el-table-column label="备注"
                         min-width="180"
                         show-overflow-tooltip
                         prop="remark"
                         align="center">
          <template slot-scope="scope">{{scope.row.remark==null ? '--' : scope.row.remark}}</template>
        </el-table-column>
        <el-table-column label="操作人"
                         width="180"
                         prop="userName"
                         align="center">
        </el-table-column>
      </el-table>
      <el-button class="button_mian"
      type="primary"
                 @click="goBack">返回</el-button>
    </el-card>

    <!-- 固定在下面 -->
    <div class="fixedB">
      <!-- 页码 -->
      <el-pagination background
                     style="margin-top: 10px;margin-left:20px"
                     @size-change="handleSizeChange"
                     @current-change="pageChange"
                     layout="total,sizes,prev,pager,next,jumper"
                     :page-size="page_size"
                     :page-sizes="[15, 30, 50, 100]"
                     :current-page="pageNum"
                     :total="total">
      </el-pagination>
    </div>
  </div>
</template>
<script>
import { showDetails } from '@/api/productInventory/productInventory'
export default {
  data() {
    return {
      list: {
        time: []
      },
      tableData: [],
      productDetails: [],
      pageNum: 1,
      page_size: 15,
      total: 0,
    }
  },
  created() {
    this.searchList()
  },
  methods: {
    searchList() {
      showDetails({
        shopId: this.$route.query.shopId,
        productId: this.$route.query.productId,
        skuId: this.$route.query.skuId,
        startCreateTime: this.list.time == null ? '' : this.list.time[0],
        endCreateTime: this.list.time == null ? '' : this.list.time[1],
        pageSize: this.page_size,
        pageNum: this.pageNum
      }).then(res => {
        console.log('明细res',res)
        this.productDetails = res.data
        this.tableData = res.data.list.list
        this.total = res.data.list.total

      })
    },
    // 重置
    reset() {
      this.list = {
        time: []
      }
      this.searchList()
    },

    //返回
    goBack() {
      this.$store.dispatch('delView', this.$route)
      this.$router.push({
        path: '/inventoryManagement/inAndOutInventory',
      })
    },
    //修改每页页数
    handleSizeChange(page) {
      this.page_size = page
      this.searchList()
    },
    //点击跳转页码
    pageChange(page) {
      this.pageNum = page
      this.searchList()
    },
  }
}
</script>
<style lang="scss" scoped>
.app-container {
  padding: 20px 20px 40px 20px;
  .product_detail {
    background: #f0f9eb;
    color: #67c23a;
  }
  .button_mian{
    margin-top:30px; 
    width: 200px;
  }
}
</style>